/* Generates mutilevel nested comments
level-1 is the first level comment with a margin of 50px
level-2 is the first level comment with a margin of 100px
and so on...
*/
/* stylelint-disable value-keyword-case*/
.gpnl-newsletter__wrapper {
  margin: 48px 0 32px;
  padding: 0 !important; }

.happy-point-block-wrap {
  height: unset; }
  .happy-point-block-wrap div.form-group {
    margin-bottom: 0; }
  .happy-point-block-wrap input.form-control {
    margin-bottom: 15px; }
  .happy-point-block-wrap .gpnl-newsletter__text {
    margin-top: 20px; }
    .happy-point-block-wrap .gpnl-newsletter__text h2 {
      margin-bottom: 15px;
      padding: 15px;
      background-color: white;
      width: max-content;
      max-width: 100%; }
    .happy-point-block-wrap .gpnl-newsletter__text p {
      line-height: unset;
      font-size: unset;
      color: white;
      width: max-content;
      padding: 15px;
      background-color: #003300;
      max-width: 100%;
      text-align: left; }
  .happy-point-block-wrap .gpnl-newsletter__form {
    margin: 15px 15px 15px 0; }
    .happy-point-block-wrap .gpnl-newsletter__form .submit-button-newsletter {
      line-height: 2;
      margin: 0; }
